// 判断 PV 是否足够
const judge_pv = function(pv, limit_pv) { 
    if (pv >= limit_pv) { return true } return false }

// 计算是否 满足 成员
const judge_member = function(len_ul, limit_member) {
    if (len_ul >= limit_member) { return true } return false }

// 判断是否 有 子类
const judge_has_children = function(user_ul, name_field) {
    if ( name_field in user_ul) { return true } return false }

// 计算购买总 Pv
const _numd_pv = function(user_item, name_field = 'product') {
    let i = 0 
    user_item[name_field].map(e => { i += (e.num * e.pv); return e })
    return i
}

/*
    获取 PV 数量
*/
const getPv = function(user_item) {
    return _numd_pv(user_item)
}


// 计算 奖金
const numd_usd = function(res, limit_member, usd) {
    return Math.floor(res.length / limit_member); return (usd * n) }

// 计算结果
const numd_result = function(user_ul, limit_pv, limit_member, name_field) {

    let pv = 0
    let res = []

    if (judge_has_children(user_ul, name_field)) {
        
        const child_one = user_ul[name_field]

        if (judge_member(child_one.length, limit_member)) {

            child_one.map(e => {
                if (judge_pv(getPv(e), limit_pv)) {

                    if (judge_has_children(e, name_field)) {
                        const child_two = e[name_field]
                        const res_two = []

                        child_two.map(o => {

                            pv = getPv(o)
                            if (judge_pv(pv, limit_pv)) {
                                res_two.push(pv)
                            }

                        })

                        if (judge_member(res_two.length, limit_member)) {
                            res.push(res_two)
                        }
                        
                    }

                }
            })

        }
    }

    return res
}

// 调用
const getActiveHouseBonus = function(user_ul, limit_pv, limit_member, usd = 100, name_field = 'children') {
    
    const res = numd_result(user_ul, limit_pv, limit_member, name_field)
    
    const u = numd_usd(res, limit_member, usd)

    console.log('获取结果 =', res)
    console.log('Usd =', u)

    return u
}

export default {
    getActiveHouseBonus
}


