<template>
    <div>
        <v-select label="用户模型" dense class="user-model" v-model="v" :items="choise" @change="getUserModel"></v-select>
    </div>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                v: 's',
                choise: [ { text: 'S', value: 's' }, { text: 'E', value: 'e' } ]
            }
        },
        methods: {
            getUserModel() {
                this.$emit('getUser_Father', this.v)
            }
        }
    }
</script>

<style lang="sass" scoped>
    .user-model
        width: 120px
</style>


