<template>
    <div>

        <v-avatar
            size="36px"
        >
            <img
            v-if="isImg"

            alt="Avatar"
            src="https://avatars0.githubusercontent.com/u/9064066?v=4&s=460"
            >

            <v-icon v-else class="avatar-icon">
                mdi-account-circle-outline
            </v-icon>
        </v-avatar>
        <span>
            <strong>{{ named }}</strong>
        </span>
    </div>
</template>

<script>
    export default {
        props: {
            isImg: {
                type: Boolean,
                default: true
            },
            named: {
                type: String,
                default: '第0代'
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    .avatar-icon
        font-size: 29px
</style>


